export const intlNumFormat = function (num, type = "decimal", curr = "usd", locale = 'en-US') {
    if(type === "decimal") {
        return new Intl.NumberFormat(locale, {
            style: "decimal",
            maximumFractionDigits: 0,
        }).format(num);
    }

    if(type === "decimal2") {
        return new Intl.NumberFormat(locale, {
            style: "decimal",
            minimumFractionDigits: 0,
            maximumFractionDigits: 2,
        }).format(num);
    }

    if(type === "currency") {
        return new Intl.NumberFormat(locale, {
            style: 'currency',
            currency: curr,
            minimumFractionDigits: 0,
            maximumFractionDigits: 0,
        }).format(num);
    }

    if(type === "compact") {
        return new Intl.NumberFormat(locale, {
            style: "currency",
            currency: curr,
            notation: "compact",
            compactDisplay: "short",
            minimumFractionDigits: 0,
        }).format(num);
    }

    if(type === "percent") {
        return new Intl.NumberFormat(locale, {
            style: "percent",
            maximumFractionDigits: 0,
        }).format(num);
    }
}

export const sumNums = arr => {
    return arr.reduce((acc, cur) => acc + Number(cur), 0);
}

export const formatGrowth = (num1, num2) => {
    return `${parseFloat(num1 / num2).toFixed(1)}x`;
}
