import { useRef, useEffect } from "react";
import * as d3 from "d3";
import { CHART_COLORS } from "../helpers/constants";
import { intlNumFormat } from "../helpers/functions";

export default function ChartLollipop({
    data,
    width,
    row,
    column,
    type,
    title,
    color
}) {
    const svgRef = useRef(null);

    useEffect(() => {
        if(!width) return;
        if(!svgRef.current) return;
        if(data?.length === 0) return;

        const VALUES = data.map(elem => Number(elem[column]));
        const DATA = [];
        const MIN = Math.min(...VALUES);
        const MAX = Math.max(...VALUES);

        data.forEach(elem => {
            DATA.push({
                key: elem[row],
                value: Number(elem[column])
            });
        });

        const canvas = svgRef.current;
        const margin = { top: 20, right: 20, bottom: 40, left: 0 };
        const outerWidth = width - 40;
        const outerHeight = 400;
        const innerWidth = outerWidth - margin.left - margin.right;
        const innerHeight = outerHeight - margin.top - margin.bottom;
        const paddingInner = 0.1;

        const xScale = d3.scaleBand()
            .range([0, innerWidth])
            .domain(DATA.map(d => d.key))
            .paddingInner(paddingInner);

        const yScale = d3.scaleLinear()
            .range([innerHeight, 0])
            .domain([0, MAX])
            .nice();
        // console.log(xScale.bandwidth())
        const svg = d3.select(canvas)
            .data([null])
            .join("svg")
                .classed("chart-lollipop__svg", true)
                .attr("width", outerWidth)
                .attr("height", outerHeight)
        .selectAll("g")
            .data([null])
            .join("g")
                .classed("svg__group", true)
                .attr("transform", `translate(${margin.left},${margin.top})`);

        svg.selectAll(".svg__group__circle-back")
            .data(DATA)
            .join("circle")
                .classed("svg__group__circle-back", true)
                .attr("r", 10)
                .attr("cx", d => xScale(d.key) + xScale.bandwidth() / 2 + 2)
                .attr("cy", d => yScale(d.value) + 10)  
                .style("fill", (d, i) => {
                    if(color) {
                        return color;
                    } else {
                        return CHART_COLORS[i];
                    }
                })
                .style("opacity", 0.4);

        svg.selectAll(".svg__group__circle-front")
            .data(DATA)
            .join("circle")
                .classed("svg__group__circle-back", true)
                .attr("r", 10)
                .attr("cx", d => xScale(d.key) + xScale.bandwidth() / 2 - 2)
                .attr("cy", d => yScale(d.value) + 10)  
                .style("fill", "none")
                .style("opacity", 1)
                .style("stroke", "black")
                .style("stroke-width", 2);

        svg.selectAll(".svg__group__line")
            .data(DATA)
            .join("rect")
                .classed("svg__group__line", true)
                .attr("x", d => xScale(d.key) + xScale.bandwidth() / 2 - 1)
                .attr("rx", 2.5)
                .attr("width", 2)
                .attr("y", d => yScale(d.value) + 20)
                .attr("height", d => innerHeight - yScale(d.value) - 20)
                .style("opacity", 1);

        svg.selectAll(".svg__group__circle-label")
            .data(DATA)
            .join("text")
                .classed("svg__group__circle-label", true)
                .text(d => intlNumFormat(d.value, type))
                .attr("x", d => xScale(d.key) + xScale.bandwidth() / 2 - 1)
                .attr("width", (d, i) => 44)
                .attr("y", d => yScale(d.value) - 5)
                .style("opacity", 1)
                .style("text-anchor", "middle");

        const xAxis = d3.axisBottom(xScale);
        svg.selectAll(".svg__group__xAxis-values")
            .data([null])
            .join("g")
                .classed("svg__group__xAxis-values", true)
                .attr("transform", `translate(0, ${innerHeight})`)
                .call(xAxis);

        return () => {
            if (canvas) {
                while (canvas.firstChild) {
                    canvas.removeChild(canvas.firstChild);
                }
            }
        }

    }, [data, width, column, row, type, color]);

    return (
        <div className="chart">
            <div className="chart__title">{title}</div>
            <svg ref={svgRef}></svg>
        </div>
    );
}
