import { useRef, useEffect } from "react";
import * as d3 from "d3";
import { intlNumFormat } from "../helpers/functions";

export default function ChartStacked({
    data,
    width,
    type,
    title,
    color
}) {
    const svgRef = useRef(null);

    useEffect(() => {
        if(!width) return;
        if(!svgRef.current) return;
        if(data?.length === 0) return;

        const subgroups = data.columns.slice(1);
        const groups = data.map(d => (d.group));
        const DATA = data;

        const canvas = svgRef.current;
        const margin = { top: 0, right: 20, bottom: 40, left: 20 };
        const outerWidth = width - 40;
        const outerHeight = 400;
        const innerWidth = outerWidth - margin.left - margin.right;
        const innerHeight = outerHeight - margin.top - margin.bottom;
        const paddingInner = 0.2;

        const xScale = d3.scaleBand()
            .range([0, innerWidth])
            .domain(groups)
            .paddingInner(paddingInner);

        const yScale = d3.scaleLinear()
            .range([innerHeight, 0])
            .domain([0, 120])
            .nice();

        const stackedData = d3.stack()
            .keys(subgroups)
            (DATA)

        const svg = d3.select(canvas)
            .data([null])
            .join("svg")
                .classed("chart-stacked__svg", true)
                .attr("width", outerWidth)
                .attr("height", outerHeight)
        .selectAll("g")
            .data([null])
            .join("g")
                .classed("svg__group", true)
                .attr("transform", `translate(${margin.left},${margin.top})`);

        svg.selectAll(".svg__group__bar-back")
            .data(stackedData)
            .join("g")
            .attr("fill", (d, i) => {
                if(i === 0) return color;
                else return "#d1d2da";
            })
            .selectAll("rect")
            .data(d => d)
            .join("rect")
                .classed("svg__group__bar-back", true)
                .attr("x", d => xScale(d.data.group) + 3)
                .attr("y", d => yScale(d[1]))
                .attr("height", d => yScale(d[0]) - yScale(d[1]))
                .attr("width", Math.min(xScale.bandwidth(), 44))
                .attr("rx", 2.5)
                .style("opacity", 0.4);

        svg.selectAll(".svg__group__bar-front")
            .data(stackedData)
            .join("g")
            .attr("fill", (d, i) => {
                if(i === 0) return "var(--green)";
                else return "var(--gray)";
            })
            .selectAll("rect")
            .data(d => d)
            .join("rect")
                .classed("svg__group__bar-back", true)
                .attr("x", d => xScale(d.data.group) - 3)
                .attr("y", d => yScale(d[1]))
                .attr("height", d => yScale(d[0]) - yScale(d[1]))
                .attr("width", Math.min(xScale.bandwidth(), 44))
                .attr("rx", 2.5)
                .style("fill", "none")
                .style("opacity", 1)
                .style("stroke", "black")
                .style("stroke-width", 2);

        svg.selectAll(".svg__group__bar-label")
            .data(stackedData)
            .join("g")
            .selectAll("text")
            .data(d => d)
            .join("text")
                .classed("svg__group__bar-label", true)
                .text((d, i) => {
                    const num = Number((d[1] - d[0]) / 100);
                    if(num) return intlNumFormat(num, type);
                    else return "";
                })
                .attr("x", d => xScale(d.data.group) + Math.min(xScale.bandwidth(), 44) / 2)
                .attr("y", d => yScale(d[1]) + 20)
                .style("opacity", 1)
                .style("text-anchor", "middle");

        const xAxis = d3.axisBottom(xScale);
        svg.selectAll(".svg__group__xAxis-values")
            .data([null])
            .join("g")
                .classed("svg__group__xAxis-values", true)
                .attr("transform", `translate(0, ${innerHeight})`)
                .call(xAxis);

        return () => {
            if (canvas) {
                while (canvas.firstChild) {
                    canvas.removeChild(canvas.firstChild);
                }
            }
        }

    }, [data, width, type, color]);

    return (
        <div className="chart">
            <div className="chart__title">{title}</div>
            <svg ref={svgRef}></svg>
            <div className="chart__legend">
                <div className="chart__legend__option">
                    <div></div>
                    <span>new grants</span>
                </div>
                <div className="chart__legend__option">
                    <div></div>
                    <span>old grants</span>
                </div>
            </div>
        </div>
    );
}
