import { useState, useEffect, useRef } from "react";
import gitcoin from "./assets/gitcoin.png";
import "./App.css";
import fileOverview from "./data/overview.csv";
import fileGrants from "./data/grants.csv";
import fileGrantsGR12 from "./data/grants_gr12.csv";
import fileFunds from "./data/funds.csv";
import fileFundsGR12 from "./data/funds_gr12.csv";
import fileAverages from "./data/averages.csv";
import fileAveragesGR12 from "./data/averages_gr12.csv";
import fileRatios from "./data/ratios.csv";
import fileRatiosGR12 from "./data/ratios_gr12.csv";
import fileRatiosGR01GR12 from "./data/ratios_gr01gr12.csv";
import fileStackedFunds from "./data/stacked_funds.csv";
import fileStackedContributions from "./data/stacked_contributions.csv";
import fileStackedGrants from "./data/stacked_grants.csv";
import fileLeaderboard from "./data/leaderboard.csv";
import fileLeaderboardGR12 from "./data/leaderboard_gr12.csv";
import { debounce } from "lodash";
import { csv } from "d3";
import {
    intlNumFormat,
    sumNums,
    formatGrowth
} from "./helpers/functions";
import { DEBOUNCE_DELAY } from "./helpers/constants";
import ScrollBar from "./components/ScrollBar";
import CardTotal from "./components/CardTotal";
import ChartSankey from "./charts/ChartSankey";
import ChartBarH from "./charts/ChartBarH";
import ChartBarW from "./charts/ChartBarW";
import ChartLollipop from "./charts/ChartLollipop";
import ChartStacked from "./charts/ChartStacked";
import Table from "./components/Table";

function App() {
    // ~~~ Event listeners vars ~~~
    const [scroll, setScroll] = useState(0);
    // ~~~ Observer vars ~~~
    const wrapperRef = useRef(null);
    const [width, setWidth] = useState(0);
    // ~~~ Data vars ~~~
    const [overviewData, setOverviewData] = useState([]);
    const [grantsData, setGrantsData] = useState([]);
    const [grantsGR12Data, setGrantsGR12Data] = useState([]);
    const [fundsData, setFundsData] = useState([]);
    const [fundsGR12Data, setFundsGR12Data] = useState([]);
    const [averagesData, setAveragesData] = useState([]);
    const [averagesGR12Data, setAveragesGR12Data] = useState([]);
    const [ratiosData, setRatiosData] = useState([]);
    const [ratiosGR12Data, setRatiosGR12Data] = useState([]);
    const [ratiosGR01GR12Data, setRatiosGR01GR12Data] = useState([]);
    const [stackedFundsData, setStackedFundsData] = useState([]);
    const [stackedContributionsData, setStackedContributionsData] = useState([]);
    const [leaderboardData, setLeaderboardData] = useState([]);
    const [leaderboardGR12Data, setLeaderboardGR12Data] = useState([]);
    const [stackedGrantsData, setStackedGrantsData] = useState([]);
    // ~~~ Fixed vars ~~~
    const total_values = overviewData.map(elem => Number(elem["sum_total"]));
    const crowdfunded_values = overviewData.map(elem => Number(elem["sum_crowdfund"]));
    const match_values = overviewData.map(elem => Number(elem["sum_match"]));
    const grants_values = overviewData.map(elem => Number(elem["count_grants"]));
    const contributions_values = overviewData.map(elem => Number(elem["count_contributions"]));
    const unique_contributors_values = overviewData.map(elem => Number(elem["count_unique_contributors"]));
    const sum_total = sumNums(total_values);
    const sum_crowdfund = sumNums(crowdfunded_values);
    const sum_match = sumNums(match_values);
    const count_grants = sumNums(grants_values);
    const count_contributions = sumNums(contributions_values);
    const count_unique_contributors = sumNums(unique_contributors_values);
    const avg_fund = sum_total / count_grants;
    const total_ath = total_values[total_values.length - 1];
    const total_last_growth = formatGrowth(total_values[total_values.length - 1], total_values[total_values.length - 2]);
    const total_first_growth = formatGrowth(total_values[total_values.length - 1], total_values[0]);
    const grants_ath = grants_values[grants_values.length - 1];
    const grants_last_growth = formatGrowth(grants_values[grants_values.length - 1], grants_values[grants_values.length - 2]);
    const grants_first_growth = formatGrowth(grants_values[grants_values.length - 1], grants_values[0]);
    const contributions_ath = contributions_values[contributions_values.length - 1];
    const contributions_last_growth = formatGrowth(contributions_values[contributions_values.length - 1], contributions_values[contributions_values.length - 2]);
    const contributions_first_growth = formatGrowth(contributions_values[contributions_values.length - 1], contributions_values[0]);
    const keysGrants = ["round_update", "category"];
    const keysFunds = ["round", "category"];

   // ~~~ Observer that reads with on resize to set width ~~~
   useEffect(() => {
    const debouncedHandleObserver = debounce(entries => {
        const newWidth = entries[0].contentRect.width;
        setWidth(newWidth);
    }, DEBOUNCE_DELAY);

    const elem = wrapperRef?.current;
    const observer = new ResizeObserver(debouncedHandleObserver);

    
    if(elem) {
        observer.observe(elem);
    }

    return () => {
        observer.unobserve(elem);
    }
}, []);

    // ~~~ Event listener for scroll ~~~
   useEffect(() => {
        window.addEventListener("scroll", handleScroll);

    return () => {
        window.removeEventListener("scroll", handleScroll);
    }
}, []);

    // ~~~ Loading data from CVS files ~~~
    useEffect(() => {
        (async function load() {
            const res = await csv(fileGrants);
            if(res?.length) {
                setGrantsData(res);
            }
        })();

        return () => {
            setGrantsData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileGrantsGR12);
            if(res?.length) {
                setGrantsGR12Data(res);
            }
        })();

        return () => {
            setGrantsGR12Data([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileOverview);
            if(res?.length) {
                setOverviewData(res);
            }
        })();

        return () => {
            setOverviewData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileFunds);
            if(res?.length) {
                setFundsData(res);
            }
        })();

        return () => {
            setFundsData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileFundsGR12);
            if(res?.length) {
                setFundsGR12Data(res);
            }
        })();

        return () => {
            setFundsGR12Data([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileAverages);
            if(res?.length) {
                setAveragesData(res);
            }
        })();

        return () => {
            setAveragesData([]);
        }
    }, []);
    
    useEffect(() => {
        (async function load() {
            const res = await csv(fileAveragesGR12);
            if(res?.length) {
                setAveragesGR12Data(res);
            }
        })();

        return () => {
            setAveragesGR12Data([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileRatios);
            if(res?.length) {
                setRatiosData(res);
            }
        })();

        return () => {
            setRatiosData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileRatiosGR12);
            if(res?.length) {
                setRatiosGR12Data(res);
            }
        })();

        return () => {
            setRatiosGR12Data([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileRatiosGR01GR12);
            if(res?.length) {
                setRatiosGR01GR12Data(res);
            }
        })();

        return () => {
            setRatiosGR01GR12Data([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileStackedFunds);
            if(res?.length) {
                setStackedFundsData(res);
            }
        })();

        return () => {
            setStackedFundsData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileStackedContributions);
            if(res?.length) {
                setStackedContributionsData(res);
            }
        })();

        return () => {
            setStackedContributionsData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileStackedGrants);
            if(res?.length) {
                setStackedGrantsData(res);
            }
        })();

        return () => {
            setStackedGrantsData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileLeaderboard);
            if(res?.length) {
                setLeaderboardData(res);
            }
        })();

        return () => {
            setLeaderboardData([]);
        }
    }, []);

    useEffect(() => {
        (async function load() {
            const res = await csv(fileLeaderboardGR12);
            if(res?.length) {
                setLeaderboardGR12Data(res);
            }
        })();

        return () => {
            setLeaderboardGR12Data([]);
        }
    }, []);

    const handleScroll = () => {
        const winScroll = document.body.scrollTop || document.documentElement.scrollTop;
        const height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
        const scrolled = (winScroll / height) * 100;
        setScroll(scrolled);
    }

    return (
        <main ref={wrapperRef}>
            <ScrollBar scroll={scroll} />
            <header>
                <img src={gitcoin} alt="gitcoin"></img>
                <h1>Rainbow Report: Gitcoin GR12</h1>
            </header>
            <section className="tldr">
                <h2>TL;DR</h2>
                <p>🌈 grant rounds are still in an <strong>uptrend</strong> reaching in GR12 new ATHs (all time highs) in terms of funding (<strong>$6.03M</strong>), number of grants (<strong>1,812</strong>) and number of contributions (<strong>421,019</strong>).</p>
                <p>🌈 For the last rounds grants that have been applying in previous rounds are taking up the majority of the raised funds as it was the case with GR12 where <strong>61%</strong> of the funds were allocated to older grants.</p>
                <p>🌈 The category with the highest number of projects that are being built and looking for funding are from the <strong>community</strong> category with a total number of grants of <strong>2,139</strong> out of which <strong>700</strong> were submitted during GR12.</p>
                <p>🌈 The <strong>community</strong> category is also the most funded one raising in total <strong>$5.2M</strong> across all grant rounds and raising <strong>$2.2M</strong> in funds during GR12.</p>
                <p>🌈 On average <strong>Building Gitcoin</strong> is the category that received the most funding across all rounds receiving <strong>$12.02K</strong> for a single grant.</p>
                <p>🌈 In GR12 the <strong>Grants Round 12</strong> category is the winning one with <strong>$11.3K</strong> per grant.</p>
                <p>🌈 The ratio of Matched to Crowdfunded for GR12 is sitting at <strong>1.05</strong> and down <strong>-61%</strong> from the ATH reached in GR05 meaning that during GR12 <strong>every $1 crowdfunded was matched with $1.05 from the matching pool</strong>.</p>
                <p>🌈 The <strong>community</strong> category is once again in the lead in terms of ratio of Matched to Crowdfunded during GR12 with a ratio of <strong>1.83</strong> resulting in a match of <strong>$1.83</strong> for every <strong>$1</strong> that was crowdfunded.</p>
                <p>🌈 <strong>Coin Center is educating policy makers about public blockchains</strong> is the grant that differentiated itself by raising <strong>$443.84K</strong> in funds during GR12 and <strong>$1.14M</strong> in total across all grant rounds.</p>
            </section>
            <section className="total">
                <h2>Progress each day adds up</h2>
                <p>
                    Gitcoin has successfully raised <strong>{intlNumFormat(sum_total, "compact")}</strong> for digital public goods out of which <strong>{intlNumFormat(sum_crowdfund, "compact")}</strong> were crowdfunded from the community and the rest consisting of <strong>{intlNumFormat(sum_match, "compact")}</strong> were from the matching pool.
                </p>
                <p>
                    The funds raised were allocated across a total of <strong>{intlNumFormat(count_grants, "decimal")}</strong> grants and it was all made possible due to <strong>{intlNumFormat(count_contributions, "decimal")}</strong> contributions resulting in an average allocation of <strong>{intlNumFormat(avg_fund, "compact")}</strong> for a single grant.
                </p>
                <div className="total__cards">
                    <CardTotal
                        title="Funds raised"
                        value={sum_total}
                        type="compact"
                    />
                    <CardTotal
                        title="Crowdfunded"
                        value={sum_crowdfund}
                        type="compact"
                    />
                    <CardTotal
                        title="Matching pool"
                        value={sum_match}
                        type="compact"
                    />
                    <CardTotal
                        title="Grants"
                        value={count_grants}
                        type="decimal"
                    />
                    <CardTotal
                        title="Contributions"
                        value={count_contributions}
                        type="decimal"
                    />
                    <CardTotal
                        title="Avg. grant fund"
                        value={avg_fund}
                        type="compact"
                    />
                </div>
            </section>
            <section>
                <h2>Trend is your friend until the end</h2>
                <p>
                    Having a look at the evolution over time it's noticeable that the grant rounds are in an <strong>uptrend</strong> reaching ATHs (all time highs) across the board in terms of funding, grants and contributions.
                </p>
                <p>
                    GR12, the most recent grant round, reached an ATH of <strong>{intlNumFormat(total_ath, "compact")}</strong> displaying a continuous growth for funds raised: <strong>{total_last_growth}</strong> growth compared to the last round and a <strong>{total_first_growth}</strong> growth since inception.
                </p>
                <ChartBarH
                    data={overviewData}
                    width={width}
                    row="round"
                    column="sum_total"
                    type="compact"
                    title="Funds across GR01 - GR12"
                />
                <p>
                    The same pattern can be spotted related to the number of grants where GR12 reached an ATH of <strong>{intlNumFormat(grants_ath, "decimal")}</strong> with a growth of <strong>{grants_last_growth}</strong> compared to GR11 and <strong>{grants_first_growth}</strong> growth since inception.
                </p>
                <ChartBarH
                    data={overviewData}
                    width={width}
                    row="round"
                    column="count_grants"
                    type="decimal"
                    title="Grants across GR01 - GR12"
                />
                <p>
                    More validation for the uptrend comes from the fact that the number of contributions are also at an ATH for the latest grants round reaching <strong>{intlNumFormat(contributions_ath, "decimal")}</strong> grants with a growth of <strong>{contributions_last_growth}</strong> compared to the previous one and an outstanding growth of <strong>{contributions_first_growth}</strong> compared to GR01.
                </p>
                <ChartBarH
                    data={overviewData}
                    width={width}
                    row="round"
                    column="count_contributions"
                    type="decimal"
                    title="Contributions across GR01 - GR12"
                />
            </section>
            <section>
                <h2>The flippening of new grants</h2>
                <p>
                    Analysing how funds are being allocated it seems that grants that have been applying in previous rounds are taking up the majority of the raised funds, raging from <strong>29%</strong> up to <strong>86%</strong>, opposed to the ones that are submitting their grant for the first time.
                </p>
                <p>
                    The same is true for GR12 as well where new grants received <strong>39%</strong> of the funding, this result is <strong>1.77x</strong> higher than the previous round GR11, indicating a potential uptrend that will balance the two of them over time.
                </p>
                <ChartStacked
                    data={stackedFundsData}
                    width={width}
                    type="percent"
                    title="Funds allocation across GR01-GR12"
                    color="#16ce7f"
                />
                <p>
                    The fact that the numbers of old grants also holds the majority has a direct impact and it is likely the reason why older grants are taking up more funding in the last rounds.
                </p>
                <p>
                    For example, in GR12 <strong>77%</strong> of the applications have also been applying in past rounds.
                </p>
                <ChartStacked
                    data={stackedGrantsData}
                    width={width}
                    type="percent"
                    title="Grants allocation across GR01-GR12"
                    color="#16ce7f"
                />
                <p>
                    There is a positive correlation between the number of grants and the contributions made by the community since both variables move in the same direction.
                </p>
                <p>
                    For example, as it was stated previously, in GR12, <strong>77%</strong> of the applications have also been applying in past rounds and also <strong>77%</strong> of the total contributions were allocated to older grants. 
                </p>
                <ChartStacked
                    data={stackedContributionsData}
                    width={width}
                    type="percent"
                    title="Contributions allocation across GR01-GR12"
                    color="#16ce7f"
                />
            </section>
            <section>
                <h2>Build, build, build</h2>
                <p>
                    There are a variaty of projects out there looking for funding and it looks like the majority are concentrated on <strong>community</strong> and <strong>dApp tech</strong> projects, the sum of these two categories making up <strong>72%</strong> of the total number of grants.
                </p>
                <p>
                    <strong>Community</strong> projects took the 1st place with a total number of <strong>{intlNumFormat(2139, "decimal")} </strong> grants and <strong>dApp tech</strong> projects came in as a close 2nd with <strong>{intlNumFormat(2128, "decimal")}</strong> grants.
                </p>
                <ChartSankey
                    width={width}
                    data={grantsData}
                    keys={keysGrants}
                    title="Flow of Grants across GR01 - GR12"
                    label="Grants"
                    type="decimal"
                />
                <p>
                    Zooming in to look at GR12 the same two categories are battling for the 1st place in terms of number of grants, respectively <strong>community</strong> grants with a total of <strong>{intlNumFormat(700, "decimal")}</strong> and <strong>dApp tech</strong> grants with a total of  <strong>{intlNumFormat(655, "decimal")}</strong>.
                </p>
                <ChartSankey
                    width={width}
                    data={grantsGR12Data}
                    keys={keysGrants}
                    title="Flow of Grants across GR12"
                    label="Grants"
                    type="decimal"
                />
            </section>
            <section>
                <h2>Fortune favors the bold</h2>
                <p>
                    A similar picture is depicted when it comes to funds as it was with the total number of grants where <strong>community</strong> projects secure 1st place thus being the most funded category raising in total <strong>{intlNumFormat(5200000, "compact")}</strong>.
                </p>
                <ChartSankey
                    width={width}
                    data={fundsData}
                    keys={keysFunds}
                    title="Flow of Funds across GR01 - GR12"
                    label="Funds"
                    type="compact"
                />
                <p>
                    Taking a closer look at the latest grants round GR12 the <strong>community</strong> category once again takes the 1st place securing a total of <strong>{intlNumFormat(2200000, "compact")}</strong> in funds.
                </p>
                <ChartSankey
                    width={width}
                    data={fundsGR12Data}
                    keys={keysFunds}
                    title="Flow of Funds across GR12"
                    label="Funds"
                    type="compact"
                />
                <p>
                    In order to have a complete overview over funding across all grant rounds it looks like <strong>building Gitcoin</strong> is the category that received the most funding respectively <strong>{intlNumFormat(12020, "compact")}</strong> for a single grant on average.
                </p>
                <ChartBarW
                    data={averagesData}
                    width={width}
                    row="category"
                    column="average_total"
                    type="compact"
                    title="Average funding across GR01 - GR12"
                    color="#d1d2da"
                />
               <p>
                    For GR12 the <strong>Grants Round 12</strong> is the category that takes the 1st place with <strong>{intlNumFormat(11300, "compact")}</strong> on average per grant and the <strong>community</strong> category lands on the 4th place with an average of only <strong>{intlNumFormat(3200, "compact")}</strong>.
                </p>
                <ChartBarW
                    data={averagesGR12Data}
                    width={width}
                    row="category"
                    column="average_total"
                    type="compact"
                    title="Average funding across GR12"
                    color="#e90d88"
                />
            </section>
            <section>
                <h2>In pursuit of the Golden Ratio</h2>
                <p>
                    In terms of analysing the ratio of Matched to Crowdfunded funds raised the ATH was reached during GR05 with a ratio of <strong>{intlNumFormat(2.72, "decimal2")}</strong> meaning that every <strong>$1</strong> crowdfunded  was matched with <strong>$2.72</strong> from the matching pool.
                </p>
                <p>
                    Grants round number 12 closed off with a ratio of <strong>{intlNumFormat(1.05, "decimal2")}</strong> which is <strong>+54%</strong> higher than GR11 and at the same time <strong>-61%</strong> lower than the ATH.
                </p>
                <ChartLollipop
                    data={ratiosData}
                    width={width}
                    row="round"
                    column="ratio"
                    type="decimal2"
                    title="Ratio of Matched to Crowdfunded across GR01-GR12"
                />
                <p>
                    Diving deeper into the categories for GR12 the <strong>community</strong> category is at the higher end with a ratio of <strong>{intlNumFormat(1.83, "decimal2")}</strong> and at the lower end is the <strong>NFTs</strong> category with a ratio of <strong>{intlNumFormat(0.56, "decimal2")}</strong>.
                </p>
                <ChartLollipop
                    data={ratiosGR12Data}
                    width={width}
                    row="category"
                    column="ratio"
                    type="decimal2"
                    title="Ratio of Matched to Crowdfunded across GR12"
                    color="#e90d88"
                />
            </section>
            <section>
                <h2>The best from the rest</h2>
                <p>
                    The grant that differentiated itself by raising <strong>$1.14M</strong> in funds across all grant rounds is <strong>Coin Center is educating policy makers about public blockchains</strong> and it comes with no surprise that is part of the <strong>community</strong> category.
                </p>
                <h3>Top 10 grants by total funds across GR01-GR12</h3>
                <Table
                    data={leaderboardData}
                    title="Top 10 grants across GR01-GR12"
                />
                <p>
                    The same grant <strong>Coin Center is educating policy makers about public blockchains</strong> secured 1st place during GR12 as well raising <strong>$443.84K</strong> in funds.
                </p>
                <h3>Top 10 grants by total funds across GR12</h3>
                <Table
                    data={leaderboardGR12Data}
                    title="Top 10 grants across GR12"
                />
            </section>
            <section>
                <h2>Closing thoughts</h2>
                <p>💭 Considering the skyrocketed popularity of <strong>NFTs</strong> one would might have thought that this interest would also be reflected in the Gitcoin grants.<br></br><br></br>
                As it was observed in the data not only that there the number of applications was low in GR12 (only <strong>140</strong> out of <strong>1,812</strong>) but the <strong>NTFs</strong> category was a the lower end in matching funds with the lowest ratio of <strong>0.56</strong> suggesting that there is a <strong>low interest</strong> in this category both from builders and contributors.</p>
                <p>💭 Those that are considering applying for a <strong>new grant</strong> for the first time should be aware of the possibility that raising the neccessary funds might not happen on the first round.</p>
                <p>What the data is displaying is that the most successful projects in raising funds took part of a process spread <strong>across multiple rounds</strong>.</p>
            </section>
        </main>
    );
}

export default App;
