import { useRef, useEffect } from "react";
import * as d3 from "d3";
import { intlNumFormat } from "../helpers/functions";

export default function ChartBarW({
    data,
    width,
    row,
    column,
    type,
    title,
    color
}) {
    const svgRef = useRef(null);

    useEffect(() => {
        if(!width) return;
        if(!svgRef.current) return;
        if(data?.length === 0) return;

        const VALUES = data.map(elem => Number(elem[column]));
        const DATA = [];
        const MIN = Math.min(...VALUES);
        const MAX = Math.max(...VALUES);

        data.forEach(elem => {
            DATA.push({
                key: elem[row],
                value: Number(elem[column])
            });
        });

        const canvas = svgRef.current;
        const margin = { top: 20, right: 20, bottom: 40, left: 140 };
        const outerWidth = width - 40;
        const outerHeight = 400;
        const innerWidth = outerWidth - margin.left - margin.right;
        const innerHeight = outerHeight - margin.top - margin.bottom;
        const paddingInner = 1;

        const xScale = d3.scaleLinear()
            .range([0, innerWidth])
            .domain([MIN, MAX])
            .nice();

        const yScale = d3.scaleBand()
            .range([0, innerHeight])
            .domain(DATA.map(d => d.key))
            .paddingInner(paddingInner);

        const svg = d3.select(canvas)
            .data([null])
            .join("svg")
                .classed("chart-bar-w__svg", true)
                .attr("width", outerWidth)
                .attr("height", outerHeight)
        .selectAll("g")
            .data([null])
            .join("g")
                .classed("svg__group-w", true)
                .attr("transform", `translate(${margin.left},${margin.top})`);

        svg.selectAll(".svg__group-w__bar-back")
            .data(DATA)
            .join("rect")
                .classed("svg__group-w__bar-back", true)
                .attr("x", 0)
                .attr("rx", 2.5)
                .attr("width", (d, i) => xScale(d.value))
                .attr("y", d => yScale(d.key))
                .attr("height", 30)   
                .style("fill", color)
                .style("opacity", 0.4);

        svg.selectAll(".svg__group-w__bar-front")
            .data(DATA)
            .join("rect")
                .classed("svg__group-w__bar-front", true)
                .attr("x", 0)
                .attr("rx", 2.5)
                .attr("width", (d, i) => xScale(d.value))
                .attr("y", d => yScale(d.key) + 4)
                .attr("height", 30)  
                .style("fill", "none")
                .style("opacity", 1)
                .style("stroke", "black")
                .style("stroke-width", 2);

        svg.selectAll(".svg__group-w__bar-label")
            .data(DATA)
            .join("text")
                .classed("svg__group-w__bar-label", true)
                .text(d => intlNumFormat(d.value, type))
                .attr("x", d => xScale(d.value) + 5)
                .attr("y", d => yScale(d.key) + 10)
                .style("opacity", 1)
                .style("alignment-baseline", "hanging");

        const yAxis = d3.axisLeft(yScale);
        svg.selectAll(".svg__group-w__xAxis-values")
            .data([null])
            .join("g")
                .classed("svg__group-w__xAxis-values", true)
                .attr("transform", `translate(${0}, ${margin.top})`)
                .call(yAxis);

        return () => {
            if (canvas) {
                while (canvas.firstChild) {
                    canvas.removeChild(canvas.firstChild);
                }
            }
        }

    }, [data, width, column, row, type, color]);

    return (
        <div className="chart">
            <div className="chart__title">{title}</div>
            <svg ref={svgRef}></svg>
        </div>
    );
}
