import "./Table.css";
import { intlNumFormat } from "../helpers/functions";

export default function Table({ data, title }) {
    return (
        <div className="table">
            <div className="table__head">
                <div>Rank</div>
                <div>Title</div>
                <div>No. of rounds</div>
                <div>Category</div>
                <div>Funds raised</div>
            </div>
            <div className="table__body">
                {
                    data.length > 0 &&
                    data.map((elem, ind) => (
                        <div
                            className="table__body__item"
                            key={ind}
                        >
                            <div>{elem.rank}</div>
                            <div>{elem.title}</div>
                            <div>{intlNumFormat(elem.count, "decimal")}</div>
                            <div>{elem.category}</div>
                            <div>{intlNumFormat(elem.total, "compact")}</div>

                        </div>
                    ))
                }
            </div>
        </div>
    );
}
